import React, { useState } from 'react';
import ReactPlayer from 'react-player';
import Toolbar from '../components/Toolbar/Toolbar';
import Poster from '../Midnight.jpg';
import './Home.css';
const Home = ({ splashScreen }) => {


  const [hideLogo, setHideLogo] = useState(false);

  return (
    <div>
      <div
        hidden={hideLogo}
        style={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          position: "fixed",
          width: "100%",
          height: '100%',
          backgroundColor: 'black'
        }}
      >
        <ReactPlayer
          url="Logoproxy.mp4"
          playing
          muted
          width="100%"
          height="100%"
          onEnded={(e) => {
            setHideLogo(true);
            splashScreen(true)
          }}
        />
      </div>
      <div hidden={!hideLogo} style={{ height: "100%" }}>
        <Toolbar />
        <div className="poster"> <img src={Poster} alt=""></img></div>
      </div>

    </div>
  );
}

export default Home;