import React from "react";
import Home from "./Containers/Home";
import { HashRouter as Router, Route } from "react-router-dom"
import AboutUs from "./Containers/AboutUs";
import ContactUs from "./Containers/ContactUs";
import Films from "./Containers/Films";
import News from "./Containers/News";
import Services from "./Containers/Services";
import FloatingActionButtons from "./components/Buttons/FloatingButton";
import SpringModal from "./components/Modal/Modal";
function App() {
  const [splash, setsplash] = React.useState(false)
  const [open, setopen] = React.useState(false)
  return (
    <div style={{ height: "100%" }}>
      <Router basename="/">
        <Route exact path="/" ><Home splashScreen={(newValue) => { setsplash(newValue); }} /></Route>
        <Route exact path="/films" component={Films} />
        <Route exact path="/news" component={News} />
        <Route exact path="/services" component={Services} />
        <Route exact path="/about_us" component={AboutUs} />
        <Route exact path="/contact_us" component={ContactUs} />
      </Router>
      <FloatingActionButtons hidden={!splash} openModal={() => { setopen(true); console.log(open) }} />
      <SpringModal openValue={open} openModal={() => { setopen(false); console.log(open) }} />
    </div>
  );
}

export default App;
